import React from "react";
import Introduce_Image from "../../config/Introduce.json";
import { motion, useAnimationControls } from "framer-motion";
import { useEffect } from "react";
import { useState } from "react";

const Banner = (props: { content: string; url: string; key: number }) => {
  return (
    <motion.div>
      <motion.div className="flex w-[800px] h-[450px] object-cover rounded-3xl shadow-round overflow-hidden">
        <img src={props?.url} key={props.key} alt={props?.content} />
      </motion.div>
    </motion.div>
  );
};

const CarouselBanner = () => {
  let imageList = Introduce_Image.data.map((data) => data.url);

  let [image1, setImage1] = useState(imageList[imageList.length - 1]);
  let [image2, setImage2] = useState(imageList[0]);
  let [image3, setImage3] = useState(imageList[1]);
  let [image4, setImage4] = useState(imageList[2]);

  const control1 = useAnimationControls();
  const control2 = useAnimationControls();
  const control3 = useAnimationControls();
  const control4 = useAnimationControls();

  let bannerIndex = 0;

  useEffect(() => {
    setInterval(() => {
      control1.start({ height: 0, transition: { duration: 0.5 } }).then(() => {
        control1.set({ height: 450 });
        control2.set({ scale: 1 });
        control3.set({ scale: 0.95 });
        control4.set({ scale: 0.9 });

        if (bannerIndex > imageList.length) bannerIndex = 0;
        else bannerIndex++;

        let trunk = imageList.shift();
        imageList.push(trunk!);

        setImage1(
          bannerIndex === 0
            ? imageList[imageList.length - 1]
            : imageList[imageList.length - 1]
        );
        setImage2(imageList[0]);
        setImage3(imageList[1]);
        setImage4(imageList[2]);
      });
      control2.start({ scale: 0.95, transition: { duration: 0.5 } });
      control3.start({ scale: 1, transition: { duration: 0.5 } });
      control4.start({ scale: 0.95, transition: { duration: 0.5 } });
    }, 5000);
  }, []);

  return (
    <div>
      <motion.div className="flex flex-col h-[520px] w-[850px] overflow-hidden">
        <div className="flex flex-col relative -top-[415px] left-5">
          <motion.img
            style={{ scale: 0.95 }}
            animate={control1}
            className="w-[800px] h-[450px] object-cover rounded-3xl shadow-round overflow-hidden"
            src={image1}
          />
          <motion.img
            animate={control2}
            className="w-[800px] h-[450px] object-cover rounded-3xl shadow-round overflow-hidden"
            src={image2}
          />
          <motion.img
            style={{ scale: 0.95 }}
            animate={control3}
            className="w-[800px] h-[450px] object-cover rounded-3xl shadow-round overflow-hidden"
            src={image3}
          />
          <motion.img
            style={{ scale: 0.9 }}
            animate={control4}
            className="w-[800px] h-[450px] object-cover rounded-3xl shadow-round overflow-hidden"
            src={image4}
          />
        </div>
        <div className="absolute h-[10px] w-[850px] bg-gradient-to-b from-white to-transparent" />
        <div className="absolute top-[765px] h-[10px] w-[850px] bg-gradient-to-t from-white to-transparent" />
      </motion.div>
    </div>
  );
};

function Introduce({ className = "" }) {
  return (
    <div className={className + " flex"}>
      <div className="flex">
        <CarouselBanner />
      </div>
      <div className="flex flex-col mt-[100px]">
        <div className="text-[40px] ml-[50px] mb-[35px]">
          <b>  </b>
        </div>
        <div className="text-[20px] ml-[90px]">
          Community Lab<b className="text-[#3CB3EC]"></b>
          
          <br />
          
          <b className="text-[#3CB3EC]"></b>
          
          <br />
          <br />
          
          <br />
          
          <b className="text-[#3CB3EC]">  </b>
          
          <br />
          <br />
          <b className="text-[15px] text-[#3CB3EC]"></b>
        </div>
      </div>
    </div>
  );
}

export default Introduce;
